:root {
  --color1: hsl(235, 21%, 11%);
  --color2: hsl(235, 24%, 19%);
  --color3: hsl(234, 39%, 85%);
  --color4: hsl(237, 14%, 26%);
  --color5: hsl(233, 14%, 35%);
  --color6: hsl(236, 33%, 92%);
}
html,
body,
.App {
  margin: 0;
  height: 100vh;
  width: 100vw;
}
* {
  box-sizing: border-box;
}
body {
  font-family: "Josefin Sans", sans-serif;
  background-color: var(--color1);
  transition: background-color 0.3s ease;
}

.bg-desktop {
  position: absolute;
  top: 0;
  left: 0;
  height: 40vh;
  background-position: center center;

  width: 100vw;
  object-fit: cover;
  z-index: -1;
}
.bg-mobile {
  display: none;
}

.App {
  display: flex;
  justify-content: center;
}
h1 {
  margin: 0;
  color: white;
  letter-spacing: 0.5rem;
}
main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 90%;
  max-width: 30rem;
  height: 100%;
}
.top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}
.top img {
  object-fit: contain;
  height: 1.8rem;
}
.item-box {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: var(--color2);
  color: var(--color3);
  min-height: 3rem;
  margin-top: 2px;
  user-select: none;
  transition: background-color 0.3s ease;
}
.item-box:first-of-type {
  margin: 0;
}
.add-todo {
  border-radius: 5px;
  border: none;
  margin-bottom: 1.5rem;
}
.items-box {
  border-radius: 5px 5px 0 0;
  background-color: var(--color4);
  transition: 0.3s ease;
  overflow-x: hidden;
  padding: 0;
  height: 50%;
  width: 100%;
  margin: 0;
}
.check-circle {
  user-select: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  background-color: var(--color4);
  transition: 0.3s ease;
  margin: 0.75rem 1.3rem;
  margin-right: 0;
}
.items-box .check-circle:hover {
  background: linear-gradient(135deg, hsl(192, 100%, 67%), hsl(280, 87%, 65%));
}
.undone {
  width: 85%;
  height: 85%;
  border-radius: 50%;
  background-color: var(--color2);
  transition: 0.3s ease;
}
input {
  padding-top: 0.2rem;
  color: var(--color3);
  transition: 0.3s ease;

  font-family: "Josefin Sans", sans-serif;
  font-size: 1rem;
  outline: none;
  margin-left: 1rem;
  background-color: transparent;
  border: none;
  flex-grow: 1;
  max-width: 100%;
  caret-color: hsl(220, 98%, 61%);
  overflow: hidden;
  white-space: nowrap;
}
.task-panel {
  font-size: 0.9rem;
  width: 100%;
  height: 3rem;
  background-color: var(--color2);
  transition: 0.3s ease;

  border-radius: 0 0 5px 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1.5rem;
  color: var(--color5);
  transition: 0.3s ease;

  border-top: 2px solid var(--color4);
}
.filter-buttons {
  width: 10rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
p {
  margin: 0;
}
.btn {
  cursor: pointer;
  user-select: none;
}
.btn:hover {
  color: var(--color6);
  transition: 0.3s ease;
}
.item-name {
  max-width: 21.563rem;
  padding: 0.75rem 1.3rem;
  padding-left: 0;
  width: calc(100% - 22.5px);
  cursor: pointer;
  padding-top: 1.05rem;
  margin-left: 1rem;
  overflow-wrap: break-word;
}
.delete-item {
  margin-top: 0.2 rem;
  cursor: pointer;
  display: none;
  height: 1.5rem;
  margin: 0.75rem 1.3rem;
  margin-left: 0;
}
.item-box:hover .delete-item {
  display: block;
}
.item-left {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
}
/* width */
::-webkit-scrollbar {
  width: 1rem;
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--color4);

  transition: 0.3s ease;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--color3);
  transition: 0.3s ease;
}
.theme {
  cursor: pointer;
}
.mobile {
  display: none;
}
@media (max-width: 600px) {
  .mobile {
    display: block;
  }
  .desktop {
    display: none;
  }
  .filter-buttons.mobile {
    color: var(--color5);
    min-height: 3rem;
    padding: 0.75rem 0;
    margin-top: 1rem;
    background-color: var(--color2);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    border-radius: 5px;
  }
  .filter-buttons.mobile div {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    min-width: 12rem;
  }
  html {
    font-size: 15px;
  }
}
@media (min-width: 1368px) {
  html {
    font-size: 20px;
  }
}
.dnd-info {
  margin-top: 1rem;
  margin-bottom: 1rem;
  width: 100%;
  text-align: center;
  color: var(--color5);
}
.App,
body,
html {
  overflow: hidden;
}
.draggable-box {
  position: relative;
}
.draggable {
  cursor: move; /* fallback if grab cursor is unsupported */
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
  height: 3rem;
  position: absolute;
  background-color: red;
  width: 22rem;
  opacity: 0.5;
  top: -0.75rem;
  left: 2rem;
}
.grabbable:active {
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}
