.bg-desktop,
.bg-mobile {
  opacity: 0; /* Establece la opacidad inicial a 0 */
  transition: opacity 0.3s ease;
}
.bg-mobile {
  display: none;
}
.light {
  /* Estilos adicionales para el tema claro */
  /* Asegura que el tema claro est√© encima del tema oscuro */
  opacity: 1; /* Establece la opacidad a 1 cuando tiene la clase 'light' */
}

.dark {
  /* Estilos adicionales para el tema oscuro */
  opacity: 1; /* Establece la opacidad a 1 cuando tiene la clase 'dark' */
}
@media (max-width: 600px) {
  .bg-desktop {
    display: none;
  }
  .bg-mobile {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 40vh;
    background-position: center center;

    width: 100vw;
    object-fit: cover;
    z-index: -1;
  }
}
